export function createTicketReservation(
  httpClient: HttpClient
): CreateTicketReservationSignature {
  return (
    ticketReservation: NonNullablePaths<
      TicketReservation,
      `tickets.${number}.quantity` | `tickets.${number}.ticketDefinitionId`,
      4
    >
  ) =>
    universalCreateTicketReservation(
      ticketReservation,
      // @ts-ignore
      { httpClient }
    );
}

interface CreateTicketReservationSignature {
  /**
   * Creates a ticket reservation.
   *
   * Reserves specified tickets for a limited time while a customer completes their purchase.
   * The reservation will have `PENDING` status and will automatically expire after the
   * event's configured reservation duration (typically 5-30 minutes) unless confirmed.
   * @param - Ticket reservation details to create.
   *
   * Must include at least one ticket line item with valid ticket definition ID
   * and quantity. Additional settings like seat assignments and pricing options
   * can be specified in the ticket info.
   * @returns The created ticket reservation.
   */
  (
    ticketReservation: NonNullablePaths<
      TicketReservation,
      `tickets.${number}.quantity` | `tickets.${number}.ticketDefinitionId`,
      4
    >
  ): Promise<
    NonNullablePaths<
      TicketReservation,
      | `status`
      | `tickets`
      | `tickets.${number}.ticketInfo.pricingPlan.orderId`
      | `tickets.${number}.ticketInfo.pricingPlan.planId`
      | `tickets.${number}.price.value`
      | `tickets.${number}.price.currency`
      | `tags.privateTags.tagIds`,
      6
    >
  >;
}

