interface DeleteTicketReservationSignature {
  /**
   * Deletes a ticket reservation permanently.
   *
   * Completely removes the reservation and releases all reserved tickets back into the inventory to available inventory. This action can't be undone.
   *
   * Call [Cancel Ticket Reservation](https://dev.wix.com/docs/api-reference/business-solutions/events/ticket-reservations/cancel-ticket-reservation) if you want to change the reservation status while keeping the record.
   * @param - ID of the ticket reservation to delete.
   */
  (ticketReservationId: string): Promise<
    NonNullablePaths<
      DeleteTicketReservationResponse,
      | `ticketReservation.status`
      | `ticketReservation.tickets`
      | `ticketReservation.tickets.${number}.ticketInfo.pricingPlan.orderId`
      | `ticketReservation.tickets.${number}.ticketInfo.pricingPlan.planId`
      | `ticketReservation.tickets.${number}.price.value`
      | `ticketReservation.tickets.${number}.price.currency`
      | `ticketReservation.tags.privateTags.tagIds`,
      7
    >
  >;
}

