interface BulkUpdateTicketReservationTagsSignature {
  /**
   * Updates tags on multiple ticket reservations.
   *
   * Allows adding or removing tags from multiple reservations in a single operation.
   * Specify reservation IDs and the tags to assign or unassign. If a tag appears
   * in both assign and unassign lists, it will be assigned.
   *
   * Use this for immediate tag updates on a known set of reservations.
   * @param - List of ticket reservation IDs to update.
   */
  (
    ids: string[],
    options?: NonNullablePaths<
      BulkUpdateTicketReservationTagsOptions,
      `assignTags`,
      2
    >
  ): Promise<
    NonNullablePaths<
      BulkUpdateTicketReservationTagsResponse,
      | `results`
      | `results.${number}.itemMetadata.originalIndex`
      | `results.${number}.itemMetadata.success`
      | `results.${number}.itemMetadata.error.code`
      | `results.${number}.itemMetadata.error.description`
      | `bulkActionMetadata.totalSuccesses`
      | `bulkActionMetadata.totalFailures`
      | `bulkActionMetadata.undetailedFailures`,
      6
    >
  >;
}

